@import '../main.scss';

.banner {
  position: relative;
  @include flex;
  border-radius: 25px;
  background-image: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)), url('../../assets/cover_home.png');
  background-position: center;
  background-size: cover;
  height: 223px;
  padding: 20px;
  max-width: 95%;
  margin: 43px auto;
  @media screen and (max-width: 550px){
    margin: 20px auto;
    height: 120px;
  }
  &__about {
    position: relative;
    @include flex;
    border-radius: 25px;
    height: 223px;
    max-width: 95%;
    margin: 43px auto;
    background-image: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)), url('../../assets/cover_about.png');
    background-position: center;
    background-size: cover;
    @media screen and (max-width: 550px){
      margin: 20px auto;
      height: 120px;
    }
  }
  p {
    color: $color-primary;
    font-size: clamp(20px, 4vw, 48px);
    font-weight: 500;
  }
}
